.weather {
  color: #fff;
  width: 100%;
  height: 100%;
  max-width: 395px;
  max-height: 675px;
  z-index: 1;
  background-color: #4440f6;
  border-radius: 4px;
  padding: 40px 15px 60px;
  position: absolute;
  top: 30px;
  right: 15px;
}

.weather.load {
  background-color: #6291e9;
}

.preload-wrapper {
  width: 0;
  height: 0;
  margin: 0;
  padding: 0;
}

.preload-wrapper.load {
  height: 100%;
  width: 100%;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

.preload {
  z-index: 3;
  width: 100px;
  height: 100px;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.preload .loader {
  width: 75px;
  height: 75px;
  border: 10px solid #fff;
  border-top-color: rgba(0, 0, 0, 0);
  border-radius: 100%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

